import React, {Component} from 'react';

export default class Blocks extends Component{
    constructor(props){
        super(props);

        this.state = {
            mouseY: 0,
        };

    }


    resizeHandle(that,e){
        const element = document.querySelector('.blocksContainer');
        console.log(element,e.clientY);
        this.setState({
            mouseY: e.clientY,
        });
    }

    resizeHandleMove(that,e){
        const element = document.querySelector('.blocksContainer');
        console.log(element,e.clientY);
        this.setState({
            mouseY: e.clientY,
        });
        console.log('HEIGHT', element.style);
        // element.style.height = '500px';
    }

    render(){

        return(
            <div className="blocks">
                <div className="blocksContainer">
                    <div
                        className="blocksTopLine"
                        onMouseDown={this.resizeHandle.bind(this,event)}
                        onMouseMove={this.resizeHandleMove.bind(this,event)}
                    />

                </div>
            </div>
        );
    }
}